import { useState } from 'react';
import './App.css';
import { SearchComponent, DataComponent } from './Components';

function App() {
  const [result, setResult] = useState([]);
  return (
    <div className="App">
      <SearchComponent
        setResult={setResult}
      />
      {result.length ? (<DataComponent
        result={result}
      />) : null}
    </div>
  );
}

export default App;
