const CORE_WEB_VITALS = [
    'cumulative_layout_shift',
    'experimental_time_to_first_byte',
    'first_contentful_paint',
    'first_input_delay',
    'interaction_to_next_paint',
    'largest_contentful_paint',
]

const createDate = (({ year, month, day }) => {
    return new Date(year, month - 1, day);
});

const createData = result => {
    const rows = [];
    result.map((r, i) => {
        const row = { id: i };
        CORE_WEB_VITALS.forEach(metric => {
            row[`origin`] = r?.record?.key?.url;
            row['firstDate'] = createDate(r?.record?.collectionPeriod?.firstDate);
            row['lastDate'] = createDate(r?.record?.collectionPeriod?.lastDate);
            const data = r?.record?.metrics[metric];
            if (!data) {
                console.log('No data for', metric, 'for URL: ', r?.record?.key?.url);
                row[`${metric}_threshold`] = 'No Data';
                row[`${metric}_p75`] = 'No Data';
                return;
            }
            row[`${metric}_threshold`] = data.histogram[0].end;
            row[`${metric}_p75`] = data.percentiles.p75;
        });
        rows.push(row);
        return false;
    });

    return rows;
}

export default function Data({ result }) {
    const rowsData = useMemo(() => createData(result), [result]);
    return (
        <Box
            sx={{
                height: 400,
                width: '80%',
                m: 5,
                '& .super-app.bad': {
                    backgroundColor: '#d47483',
                    fontWeight: '500',
                },
                '& .super-app.good': {
                    backgroundColor: 'rgba(157, 255, 118, 0.49)',
                    fontWeight: '500',
                },
            }}
        >
            <DataGrid
                rows={rowsData}
                columns={columns}
                initialState={{
                    pagination: {
                        paginationModel: {
                            pageSize: 5,
                        },
                    },
                }}
                slots={{
                    toolbar: GridToolbar,
                }}
                pageSizeOptions={[5]}
                disableRowSelectionOnClick
                getRowClassName={(params) =>
                    params.indexRelativeToCurrentPage % 2 === 0 ? 'even' : 'odd'
                }
                sx={{
                    boxShadow: 2,
                    border: '1px solid grey',
                    '& .MuiDataGrid-columnHeaderTitle': {
                        fontWeight: 600
                    },
                    '& .MuiDataGrid-columnSeparator': {
                        visibility: 'visible',
                        color: 'grey'
                    },
                }}
            />
            <Stack sx={{ width: '100%', mt: 1 }}>
                <Alert severity="info">
                    <AlertTitle>Info</AlertTitle>
                    <Box sx={{ display: 'flex', mt: 1 }}>
                        <Box sx={{ backgroundColor: 'rgba(157, 255, 118, 0.49)', height: '20px', width: '35px', mr: 1 }} />
                        <span>The 75th percentile of metric <strong>passes</strong> the Core Web Vitals "good" threshold when P75 {`<`} Threshold.</span>
                    </Box>
                    <Box sx={{ display: 'flex', mt: 1 }}>
                        <Box sx={{ backgroundColor: '#d47483', height: '20px', width: '35px', mr: 1 }} />
                        <span>The 75th percentile of metric <strong>does not pass</strong> the Core Web Vitals "good" threshold when P75 {`>`} Threshold.</span>
                    </Box>
                </Alert>
            </Stack>
        </Box>
    );
}