import { useState } from 'react';
import axios from 'axios';
import { styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import { LoadingButton } from '@mui/lab';
import MuiGrid from '@mui/material/Grid';
import { FormControl, FormLabel, Stack, Alert } from '@mui/material';

const Grid = styled(MuiGrid)(({ theme }) => ({
    width: '80%',
    ...theme.typography.h2,
    '& [role="separator"]': {
        margin: theme.spacing(0, 2),
    },
}));

const StyledDiv = styled('div')({
    display: 'inline-flex'
});

const StyledLabel = styled(FormLabel)({
    paddingRight: '10%',
    alignSelf: 'center'
});

export default function Search({ setResult }) {

    const [inputValue, setInputValue] = useState('');
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const handleSearch = async () => {
        try {
            setLoading(true);
            setError(null);
            setResult([]);

            const urlArray = inputValue.split(',').map((url) => url.trim()).filter(url => url);

            const promises = urlArray.map((url) =>
                axios.post(`https://chromeuxreport.googleapis.com/v1/records:queryRecord?key=AIzaSyCHRtu0BTDqGt_JC5P6e1KWHsdsHBduj70`, {
                    url
                })
            );

            const responses = await Promise.all(promises);

            const data = responses.map(response => response.data);

            setResult(data);
        } catch (error) {
            setError(error.message ? error.message + ` and message: ` + error?.response?.data?.error?.message : 'An error occurred while fetching data');
        } finally {
            setLoading(false);
        }
    };

    const handleInputChange = (event) => {
        // Update the state with the current input value
        setInputValue(event.target.value);
    };

    return (
        <Grid container>
            <Grid item xs={12}>
                <FormControl fullWidth>
                    <StyledDiv>
                        <StyledLabel htmlFor="url">URL:</StyledLabel>
                        <TextField
                            id="url"
                            required
                            sx={{ width: '70%', pr: '10%' }}
                            value={inputValue}
                            onChange={handleInputChange}
                        />
                        <LoadingButton
                            color="inherit"
                            sx={{ borderRadius: 2, borderColor: 'revert', float: 'right', textTransform: 'none' }}
                            size="large"
                            variant="outlined"
                            onClick={handleSearch}
                            loading={loading}
                            disabled={!inputValue}
                        >
                            Search
                        </LoadingButton>
                    </StyledDiv>
                    {error && <Stack sx={{ width: '100%', mt: 1 }} spacing={2}>
                        <Alert severity="error">{error}</Alert>
                    </Stack>}
                </FormControl>
            </Grid>
        </Grid>
    )
}